// DC 
import React from "react";
import isrc from "./ImgSrc";

const Logo = (props) => {
    // props.gb : ,

    // :     !
    const mystyle = {
        top: {
            width: "45px",
            height: "45px",
            marginRight: "30px",
            borderRadius: "50%",
            // outline:"3px solid lime"
        },
        bottom: {
            height: "80px",
        },
    };

    let istyle = {
        top: "45px",
        bottom: "80px",
    };

    return (
        <h1 style={mystyle[props.gb]}>
            <img src={isrc.logo} style={{ width: istyle[props.gb] }} />
        </h1>
    );
}; //////////// Logo ////////////////

export default Logo;
