import { useEffect } from "react";
import { useLocation } from "react-router-dom";

//    
//   <BrowserRouter> 
// <ScrollTop />   !(import !)
//  PJ index.js !

export default function ScrollTop(){
    //      
    const { pathname } = useLocation();

    //      
    //      
    //   ? useEffect!
    // useEffect(,[]) ->  !
    useEffect(()=>{
        //     
        window.scrollTo(0,0);
    }, [pathname]);

    // console.log(pathname);

    //      
    //    null 
    return null;
} 