///    - .js
import React,{useState} from "react";
import $ from "jquery";
import "./css/member.css";

/* 
    [  : Hook -  ? ]
    1.  -    
               
            !
    2. 
        1)   import  -> useState
        2) useState()  
        : 
             = useState()
        :
            const [,set] = useState()
            -> set    !
            -> set() :    !( )
        3) 
            - useState     
            -  set   
            useState     
              !
        4) 
            -       
                   !
*/

//    /////////
function jqFn() {
    $(() => {}); //////// jQB ///////////
} ////////////// jQFn ///////////

function Member() {

    //  :      !
    //                !

    // [  useState   ]
    // [ 1.   ]
    // 1. 
    const [userId,setUserId] = useState("");
    // 2. 
    const [pwd,setPwd] = useState("");
    // 3. 
    const [chkPwd,setChkPwd] = useState("");
    // 4. 
    const [userName,setUserName] = useState("");
    // 5. 
    const [email,setEmail] = useState("");

    // [ 2.   ]
    // ->  :   (false)
    // 1. 
    const [userIdError,setUserIdError] = useState(false);
    // 2. 
    const [pwdError,setPwdError] = useState(false);
    // 3. 
    const [chkPwdError,setChkPwdError] = useState(false);
    // 4. 
    const [userNameError,setUserNameError] = useState(false);
    // 5. 
    const [emailError,setEmailError] = useState(false);


    // [ 3.    ]
    // 1.   
    const changeUserId = e => { // e - 
        // 1.   ( !)
        const valid = /^[A-Za-z0-9+]{5,}$/;

        // 2.   : e.target ->   
        console.log(e.target.value);

        // 3.   if
        // :   true? ! false()
        // .test() ->    
        // : true  false / false  true
        if(valid.test(e.target.value))
            setUserIdError(false); // !
        else
            setUserIdError(true); // !

        // 4.  useerId     !
        setUserId(e.target.value);

    }; /////////////// changeUserId ////////////////

    // 2.   
    const changePwd = e => { // e - 
        // 1.  ( !)
        const valid = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/;

        // 2.   : e.target ->   
        console.log(e.target.value);

        // 3.   if
        // :   true? ! false()
        // .test() ->    
        // : true  false / false  true
        if(valid.test(e.target.value))
            setPwdError(false); // !
        else
            setPwdError(true); // !

        // 4.  useerId     !
        setPwd(e.target.value);


    }; ///////////// changePwd ///////////////////






    return (
        <>
            {/*  */}
            <section className="membx">
                <h2>Member</h2>
                <form>
                    {/* 1. */}
                    <label> : </label>
                    <input type="text" maxLength="20" placeholder=" " value={userId} onChange={changeUserId} />
                    {
                        //    
                        //  &&  ->  true 
                        userIdError &&
                        <div className="msg">
                            <small style={{ color: "red", fontSize: "10px" }}>
                                  5      !
                            </small>
                        </div>

                        // value={userId}  setUserId 
                        //   !

                        // onChange={changeUserId} 
                        // -> change  changeUserId !
                    }

                    {/* 2. */}
                    <label> : </label>
                    <input type="password" maxLength="20" placeholder=" " value={pwd} onChange={changePwd} />
                    {
                        //    
                        //  &&  ->  true 
                        pwdError &&                        
                        <div className="msg">
                            <small style={{ color: "red", fontSize: "10px" }}>
                             8     
                                .
                            </small>
                        </div>
                    }

                    
                    {/* 3. */}
                    {/* 4. */}
                    {/* 5. */}
                    {/* 6. */}
                </form>
            </section>

            {/*   JS */}
            {jqFn()}
        </>
    );
}

export default Member;
