// DC Comics  
import React from "react";
import VidIntro from "./modules/VidIntro";

const Comics = (props) => {
    {console.log(props.sub)}


    //   
    const tit_data = [
        " LATEST COMICS & GRAPHIC NOVELS  ",
        "DC UNIVERSE INFINITE",
        "ALL COMICS SERIES",
    ];

    return(
        <>
            <h1>{tit_data[props.sub]}</h1>
            <VidIntro pg="COMICS" mm="on" />
        </>
    );
}; /////////// Comics //////////////

export default Comics;